.marker-icon-2 {
  width: 40px;
  height: 40px;
  position: relative;
  z-index: 99;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  color: white;
  cursor: pointer;
  transition: 0.4s;
  left: 0px;
}

.marker-icon-2::after {
  position: absolute;
  content: "";
  top: 0px;
  left: 0px;
  width: 40px;
  height: 40px;
  background: url(/simon/static/media/circle1.e0642482.png);
  background-size: cover;
  -webkit-animation-name: spin;
  animation-name: spin;
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.marker-text {
  position: relative;
  width: 200px;
  font-size: 10px;
  padding: 0px;
  border: 1px solid #559cc5;
  border: 1px solid var(--main);
  -webkit-transform: scale(1, 0);
  transform: scale(1, 0);
  margin-top: 10px;
  background-color: transparent;
  border-radius: 0px;
  border-image-source: url(data:image/png;base64,);
  border-image-slice: 45% fill;
  border-image-width: 19px;
  border-image-repeat: repeat stretch;
  border-image-outset: 0;
  border-color: transparent;
  /* padding: 5px 10px; */
  color: cyan !important;
  font-size: 12px;
  transition: 0.4s;
  cursor: pointer;
  z-index: 5;
}

.marker:hover .marker-text {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
}

.marker:hover .marker-icon-2 {
  left: -147px;
  top: 7px;
}

.marker:hover .marker-text::after {
  left: -80px;
  top: -57px;
}

.marker-text::after {
  position: absolute;
  content: "";
  top: -64px;
  left: calc(50% - 32px);
  z-index: 0;
  width: 80px;
  height: 80px;
  background: url(/simon/static/media/active.453e4f80.png);
  background-size: cover;
}

.overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 1);
  z-index: 9999;
  transition: 1s;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.spike {
  position: relative;
}

.spike::after {
  display: inline-block;
  width: 4px;
  position: absolute;
  content: "";
  height: 20px;
  background-color: white;
  -webkit-transform: skew(-45deg);
  transform: skew(-45deg);
  top: 4px;
  left: -15px;
  box-shadow: 0 0 12px 2px rgb(255 255 255), 0px 0px 5px 1px rgb(250 250 2);
}

.loader {
  width: 150px;
  -webkit-animation-name: spin;
  animation-name: spin;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}

